.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

    
* {
  box-sizing: border-box;
  font-family: 'Wendy One', cursive;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
  background-image: url("../src/images/dane-deaner-571400-unsplash.jpg")
}